.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    margin: 20px 0 40px 0;
    height: 75px;
    width: 290px;
}

.startBtn {
    background: rgb(0,119,181);
    background: linear-gradient(180deg, rgba(0,119,181,1) 0%, rgba(14,103,149,1) 100%);
    border: none;
    border-radius: 4px;
    box-shadow: 0 4px 4px 0 #00000040;
    color: white;
    cursor: pointer;
    font-size: 16px;
    line-height: 36px;
    text-align: center;
    height: 36px;
    width: 230px;
    transition: all ease-in 0.1s;
}
.startBtn:hover {
    transform: scale(1.02);
    box-shadow: 0 6px 6px 0 #00000040;
}

.startBtn:active {
    transform: scale(0.98);
    box-shadow: 0 2px 2px 0 #00000040;
}