.wrapper {
    display: grid;
    grid-template-columns: 290px 1fr;
    grid-column-gap: 40px;
    grid-template-rows: 1fr;
    grid-template-areas: "sideBar content";
    margin: 40px auto 0;
    width: 1200px;
}

.sideBar {
    grid-area: sideBar;
}

.content {
    grid-area: content;
    background-color: white;
    height: 655px;
    width: 850px;
}

